<template>
    <div class="color-data row">
        <div class="col-xl-4">
            <div class="input-group">
                <div class="input-group-prepend">
                    <span class="input-group-text"></span>
                </div>
                <select class="form-control" v-model="colorMode">
                    <option v-for="(c, index) in colorModes" v-bind:key="index" v-bind:value="index">
                        {{ c.text }}
                    </option>
                </select>
            </div>
        </div>
        <div class="col-xl-8">
            <table class="example-table table table-borderless">
                <td class="table-danger">
                    ~<br>
                    {{ percentText[colorMode] }}
                </td>
                <template v-for="(d, index) in colorModes[colorMode].description" v-bind:key="index">
                    <td v-bind:class="classes[index]">
                        {{ d }}
                    </td>
                </template>
            </table>
        </div>
    </div>
</template>

<script>
    
export default {
	data () {
		return {
            classes: ['table-success', 'table-yellow', 'table-warning', 'table-danger'],
            colorModes: [
                {
                    text: '()',
                    description: ['=100%', '>50%', '>0%', '=0%']
                },
                {
                    text: '()',
                    description: ['', '', '', '']
                },
                {
                    text: '()',
                    description: ['>100', '', '', '']
                }
            ],
            colorMode: 0,
            percentText: ['','','']
        }
    },
    created: function () {
        this.$emit('update', this.colorMode)
    },
    watch: {
        'colorMode': function (){
            this.$emit('update', this.colorMode)
        },
    }
}
</script>

<style>
.example-table td{
    padding-left: 3px;
    padding-right: 3px;
	vertical-align: middle;
    width: 180px;
}

.example-table{
    margin-bottom: 0px;
	text-align: center;
    width: auto;
}

.table-yellow{
	background-color: #FFFFAA;
}

.color-data {
    margin-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px;
}

.color-data div {
    padding-left: 0px;
    padding-right: 0px;
}
</style>